import React, { Component } from 'react';
import vocabularyData from '../../data/vocabulary.json';

class Main extends Component {
    render() {

        const Present = vocabularyData.map((data, index) => {
            return <div> {data.present} </div>
        })
        const bnMeaning = vocabularyData.map((data, index) => {
            return <div> {data.bnMeaning} </div>
        })
        const Past = vocabularyData.map((data, index) => {
            return <div> {data.past} </div>
        })
        const pastParticiple = vocabularyData.map((data, index) => {
            return <div> {data.pastParticiple} </div>
        })

        return (
            <div className="container mt-5">
                <h2 class="text-center"> Verbs in Daily Uses </h2>
                <div class="table-responsive">
                    <table class="table table-sm table-hover text-center">
                        <thead>
                            <tr>
                            <th scope="col">Present</th>
                            <th scope="col">Bangla Meaning</th>
                            <th scope="col">Past</th>
                            <th scope="col">Past Participle</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                            <th scope="row">  {Present} </th>
                            <td> {bnMeaning} </td>
                            <td> {Past} </td>
                            <td> {pastParticiple} </td>
                            </tr>
                        </tbody>
                    </table>    
                </div>                
            </div>
        );
    }
}

export default Main;